import React from "react";
import {BrowserRouter as Router, Route, Switch} from "react-router-dom";
import Detail from "./routes/Detail"
import Home from "./routes/Home"

function App() {
    return (
        <Router>
            <Switch>
                <Route path="/about-us">
                    <h1>Hello</h1>
                </Route>
                <Route path="/movie/:id">
                    <Detail/>
                </Route>
                <Route path="/">
                    <Home/>
                </Route>
            </Switch>
        </Router>
    );
}

export default App;